const actions = {
  getCustomerPriceLists: (page, search, filter) => ({
    type: "GET_CUSTOMER_PRICE_LISTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createCustomerPriceList: () => ({
    type: "CREATE_CUSTOMER_PRICE_LIST_REQUEST"
  }),
  getItemsForCustomerPriceList: (id_location, search) => ({
    type: "GET_FILTER_ITEMS_FOR_CUSTOMER_PRICE_LIST_REQUEST",
    payload: { id_location: id_location, search: search }
  }),
  getCustomersForCustomerPriceList: search => ({
    type: "GET_CUSTOMERS_FOR_CUSTOMER_PRICE_LIST_REQUEST",
    payload: { search: search }
  }),
  storeCustomerPriceList: data => ({
    type: "STORE_CUSTOMER_PRICE_LIST_REQUEST",
    payload: { body: data }
  }),
  editCustomerPriceList: id_customer_pricelist => ({
    type: "EDIT_CUSTOMER_PRICE_LIST_REQUEST",
    payload: { id_customer_pricelist: id_customer_pricelist }
  }),
  updateCustomerPriceList: (id_customer_pricelist, data) => ({
    type: "UPDATE_CUSTOMER_PRICE_LIST_REQUEST",
    payload: {
      id_customer_pricelist: id_customer_pricelist,
      body: data
    }
  }),
  deactivateCustomerPriceList: id_customer => ({
    type: "DEACTIVATE_CUSTOMER_PRICE_LIST_REQUEST",
    payload: { id_customer }
  }),
  activateCustomerPriceList: id_customer => ({
    type: "ACTIVATE_CUSTOMER_PRICE_LIST_REQUEST",
    payload: { id_customer }
  }),
  rejectCustomerPriceList: (id_customer_pricelist, data) => ({
    type: "REJECT_CUSTOMER_PRICE_LIST_REQUEST",
    payload: { id_customer_pricelist, body: data }
  }),
  confirmCustomerPriceList: id_customer_pricelist => ({
    type: "CONFIRM_CUSTOMER_PRICE_LIST_REQUEST",
    payload: { id_customer_pricelist }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};
export default actions;
