class ModalUnprocessedPriceList extends Component {
  state = {
    filter: {
      id_customer: null,
      id_location: null,
      transaction_status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    id_customer_pricelist: null,
    search: null
  };

  componentDidUpdate(prevProps) {
    const { id_location } = this.props;

    if (prevProps.id_location !== id_location) {
      this.handleLocationChange(id_location);
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: id_location
        }
      });
    }
    if (this.state.id_customer_pricelist) {
      this.props.history.replace(
        redirecTo + "/update/" + this.state.id_customer_pricelist
      );
    }
  }

  handleSelect = value => {
    this.setState({ id_customer_pricelist: value });
    console.log(value);
  };

  render() {
    let columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "pricelist_code",
        rowKey: "pricelist_code",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "ordered";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }
          return (
            <div style={{ display: "flex" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_customer_pricelist);
                  }}
                  href="javascript:;"
                >
                  {text}
                </a>
              </Tooltip>
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "priceListType" }),
        dataIndex: "id_customer_type",
        rowKey: "id_customer_type",
        render: text => (
          <div>
            {text ? (
              <span>
                {<IntlMessages id="sidebar.customerType" />}
                {/* <Badge
                  style={{ backgroundColor: "#87d068" }}
                  count="Supplier Type"
                />{" "} */}
              </span>
            ) : (
              <span>
                {<IntlMessages id="customerPriceLists.customerName" />}
                {/* <Badge style={{ backgroundColor: "#2db7f5" }} count="Supplier" />{" "} */}
              </span>
            )}
          </div>
        )
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        render: (text, record) => (
          <div>
            {text ? (
              <span>{text} </span>
            ) : (
              <span>
                {record.customer_type
                  ? record.customer_type.customer_type_name
                  : record.customer_type_name}{" "}
              </span>
            )}
          </div>
        )
      },
      {
        title: getLanguage({ id: "startDate" }),
        dataIndex: "start_date",
        rowKey: "start_date",
        render: text => {
          return <span>{moment(text).format("DD MMMM YYYY")}</span>;
        }
      },
      {
        title: getLanguage({ id: "endDate" }),
        dataIndex: "expired_date",
        rowKey: "expired_date",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },

      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name"
      },
      // {
      //   title: "Last Update",
      //   dataIndex: "updated_at",
      //   rowKey: "updated_at",
      //   render: text => (
      //     <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      //   )
      // },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "confirmed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "rejected" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "pending" });
              break;
          }
          return (
            <div style={{ display: "flex" }}>
              <StatusTag className={className}>{display}</StatusTag>
            </div>
          );
        }
      }
    ];

    const { customer_pricelist } = this.props.customerPriceList.pricelist_info;

    // const { current_page, page_size, total } = this.props.customerPriceList;

    const dataSource = [];
    if (customer_pricelist) {
      Object.keys(customer_pricelist).map((item, index) => {
        var customer_type_name = "";
        if (customer_pricelist[item].pricelist_type === 2) {
          const { customer_types } = this.props.customerPriceList;
          const findSuppType = customer_types.find(
            data =>
              data.id_customer_type ===
              customer_pricelist[item].id_customer_type
          );
          if (findSuppType) {
            customer_type_name = findSuppType.customer_type_name;
          }
        }
        return dataSource.push({
          ...customer_pricelist[item],
          customer_type_name,
          key: item
        });
      });
    }

    return (
      <Modal
        title={<IntlMessages id="customerPriceLists.pageHeader" />}
        visible={this.props.visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={this.props.handleModal}
        width="95%"
        okText={getLanguage({ id: "button.ok" })}
        cancelText={getLanguage({ id: "button.cancel" })}
      >
        <Table dataSource={dataSource} columns={columns} />
      </Modal>
    );
  }
}

export default compose(connect(null, actions))(ModalUnprocessedPriceList);
