import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import moment from "moment";

// import component
import Button from "../../../../components/uielements/button";
import CardWrapper, {
  Box,
  StatusBadge,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import ModalUnprocessedPriceList from "./modalUnprocessedPriceList";
import PageHeader from "../../../../components/utility/pageHeader";
// import Popconfirms from "../../../../components/feedback/popconfirm";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import notification from "../../../../components/notification";
import {
  Col,
  Collapse,
  // DatePicker,
  Empty,
  Icon,
  Input,
  Modal,
  Row,
  Select,
  Tooltip
} from "antd";

// import action
import actions from "../../../../redux/promotion/customerPriceList/actions";
import getLanguage from "../../../App/MyLanguange";

const Option = Select.Option;
const Panel = Collapse.Panel;
// const RangePicker = DatePicker.RangePicker;
const Search = Input.Search;
const confirm = Modal.confirm;

class CustomerPriceList extends Component {
  state = {
    filter: {
      id_customer: null,
      id_customer_pricelist: null,
      id_customer_type: null,
      id_location: null,
      pagination: null,
      status: null
    },
    search: null,
    searchCust: null,
    viewedPL: false,
    visibleModalUnprocessedPriceList: false
  };

  componentDidMount() {
    const {
      getCustomerPriceLists,
      getCustomersForCustomerPriceList
    } = this.props;
    getCustomersForCustomerPriceList();
    getCustomerPriceLists();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.customerPriceList.error) {
      notification("error", this.props.customerPriceList.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.customerPriceList.success) {
      notification("success", this.props.customerPriceList.success);
      this.props.clearSuccess();
    }

    // display un-processed price list: triggered by pricelist info from foreign keys
    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    const { pricelist_info } = this.props.customerPriceList;

    if (
      pricelist_info &&
      !this.state.viewedPL &&
      permissions.includes("Confirm Customer Pricelist")
    ) {
      this.showPriceList();
      this.setState({ viewedPL: true });
    }
  }

  // Inisiasi kolom tambahan untuk edit Customer Price List
  listAction = {
    title: "",
    dataIndex: "id_customer_pricelist",
    rowKey: "id_customer_pricelist",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        {/* Update tooltip */}
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>
      </div>
    )
  };

  clearFilterState = () => {
    this.setState({
      filter: {
        id_customer: null,
        id_customer_pricelist: null,
        id_customer_type: null,
        id_location: null,
        pagination: null,
        status: null
      }
    });
  };

  clearFilterSearchState = () => {
    this.setState({
      filter: {
        id_customer: null,
        id_customer_pricelist: null,
        id_customer_type: null,
        id_location: null,
        pagination: null,
        status: null
      },
      search: null
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getCustomerPriceLists } = this.props;
    const { filter } = this.state;
    let string = "";

    Object.keys(filter).forEach((key, index) => {
      if (filter[key]) {
        string
          ? (string += "&" + key + "=" + filter[key])
          : (string += key + "=" + filter[key]);
      }
    });

    this.setState({ search: null });
    getCustomerPriceLists(1, null, string);
  };

  // handle Search/Typing Customer Name/Code
  handleSearchCustomer = value => {
    const { getCustomersForCustomerPriceList } = this.props;
    this.setState({ searchCust: value });
    getCustomersForCustomerPriceList(value);
  };

  // handle Select Customer
  handleSelectCustomer = value => {
    const { searchCust } = this.state;
    let setStateData = {};

    if (searchCust) {
      const { getCustomersForCustomerPriceList } = this.props;
      getCustomersForCustomerPriceList();
      setStateData.searchCust = null;
    }
    setStateData.id_customer = value;
    this.setState(setStateData);
    this.handleFilterChange("id_customer", value);
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // handle unprocessed price list modal
  handleModalUnprocessedPriceList = () => {
    const { visibleModalUnprocessedPriceList } = this.state;
    this.setState({
      visibleModalUnprocessedPriceList: !visibleModalUnprocessedPriceList
    });
  };

  // Handle pagination
  handlePageChange = page => {
    const { getCustomerPriceLists } = this.props;
    const { search, filter } = this.props.customerPriceList;
    getCustomerPriceLists(page, search, filter);
  };

  // Handle Reset Filter and Search
  handleResetFilterSearch = () => {
    const { getCustomerPriceLists } = this.props;
    this.clearFilterSearchState();
    getCustomerPriceLists();
  };

  handleSearch = value => {
    const { getCustomerPriceLists } = this.props;
    this.clearFilterState();
    getCustomerPriceLists(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  showPriceList = () => {
    const { pricelist_info } = this.props.customerPriceList;

    confirm({
      title: pricelist_info.title,
      content: pricelist_info.content,
      // "There is one or more unprocessed pricelist. Do you want to process?",
      icon: <Icon type="info-circle" />,
      okText: pricelist_info.ok_button,
      cancelText: pricelist_info.cancel_button,
      onOk: () => {
        this.handleModalUnprocessedPriceList();
      },
      onCancel() {}
    });
  };

  render() {
    const {
      customerPriceLists,
      customers,
      customer_types,
      locations,
      loadingCustomers,
      loadingGetCP,
      loadingForeign,
      pricelist_info
    } = this.props.customerPriceList;

    // --- init variable
    const dataSource = [];

    // --- ambil data user dari state global:auth
    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // --- Pagination Setup
    const { current_page, page_size, total } = this.props.customerPriceList;

    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "pricelist_code",
        rowKey: "pricelist_code",
        width: "15%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "ordered";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }
          return (
            <div style={{ display: "flex" }}>
              <StatusBadge className={className} />
              {text}
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "priceListType" }),
        dataIndex: "id_customer_type",
        rowKey: "id_customer_type",
        width: "12%",
        render: text => (
          <div>
            {text ? (
              <span>{<IntlMessages id="sidebar.customerType" />}</span>
            ) : (
              <span>{<IntlMessages id="customerCode" />}</span>
            )}
          </div>
        )
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer.customer_name",
        rowKey: "customer.customer_name",
        width: "15%",
        render: (text, record) => (
          <div>
            {text ? (
              <span>{text} </span>
            ) : (
              <span>{record.customer_type.customer_type_name} </span>
            )}
          </div>
        )
      },
      {
        title: getLanguage({ id: "startDate" }),
        dataIndex: "start_date",
        rowKey: "start_date",
        width: "12%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },
      {
        title: getLanguage({ id: "endDate" }),
        dataIndex: "expired_date",
        rowKey: "expired_date",
        width: "12%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },

      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "20%"
      },
      // {
      //   title: getLanguage({id: "lastUpdate"}),
      //   dataIndex: "updated_at",
      //   rowKey: "updated_at",
      //   width: "20%",
      //   render: text => (
      //     <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      //   )
      // },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by"
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        align: "center",
        width: "10%",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "confirmed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "rejected" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "pending" });
              break;
          }
          return (
            // <div style={{ display: "flex" }}>
            <StatusTag className={className}>{display}</StatusTag>
            // </div>
          );
        }
      }
    ];

    // --- tampil kolom Update apabila user punya otorisasi untuk Update price List
    if (
      permissions.includes("Update Customer Pricelist") &&
      columns.length === 8
    ) {
      columns.push(this.listAction);
    }

    // map data dari state global ke dataSource: Table
    Object.keys(customerPriceLists).map((customerPriceList, index) => {
      return dataSource.push({
        ...customerPriceLists[customerPriceList],
        key: customerPriceList
      });
    });

    return (
      <LayoutWrapper>
        {pricelist_info ? (
          <ModalUnprocessedPriceList
            history={this.props.history}
            handleModal={this.handleModalUnprocessedPriceList}
            customerPriceList={this.props.customerPriceList}
            visible={this.state.visibleModalUnprocessedPriceList}
          />
        ) : null}
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="customerPriceLists.pageHeader" />}
          >
            {permissions.includes("Create Customer Pricelist") ? (
              <Link to={`${this.props.match.path}/add`}>
                <Button
                  loading={loadingForeign}
                  disabled={loadingForeign}
                  type="primary"
                  className="mateAddInvoiceBtn"
                >
                  <IntlMessages id="customerPriceLists.button.add" />
                </Button>
              </Link>
            ) : null}
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) +
                ", " +
                getLanguage({ id: "sidebar.customer" }) +
                ", " +
                getLanguage({ id: "sidebar.location" }) +
                ", " +
                getLanguage({ id: "createdBy" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>
            <div
              style={{
                // width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetFilterSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          {/* Filter */}
          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="customers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      // allowClear={true}
                      loading={loadingForeign || loadingCustomers}
                      filterOption={false}
                      optionFilterProp="children"
                      onSelect={this.handleSelectCustomer}
                      onSearch={this.handleSearchCustomer}
                      placeholder={
                        <IntlMessages id="salesOrders.filter.select.customer" />
                      }
                      showSearch
                      style={{ width: "95%" }}
                      value={
                        this.state.searchCust
                          ? this.state.searchCust
                          : this.state.filter.id_customer
                          ? this.state.filter.id_customer
                          : []
                      }
                      //showArrow={false}
                    >
                      {customers.map((data, key) => (
                        <Option key={key} value={data.id_customer}>
                          {`${data.customer_code} (${data.customer_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.customerType" />
                    </label>
                    <br />
                    {customer_types ? (
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_customer_type"
                        )}
                        value={
                          this.state.filter.id_customer_type
                            ? this.state.filter.id_customer_type
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="customers.filter.select.customerType" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {customer_types.map((data, key) => (
                          <Option key={key} value={data.id_customer_type}>
                            {`${data.customer_type_code} (${data.customer_type_name})`}
                          </Option>
                        ))}
                      </Select>
                    ) : null}
                  </Col>
                </Row>
                <Row
                  justify="start"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    {locations ? (
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_location"
                        )}
                        value={
                          this.state.filter.id_location
                            ? this.state.filter.id_location
                            : []
                        }
                        style={{ width: "95%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.filter.select.location" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    ) : null}
                  </Col>
                  <Col span={3}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "90%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </Col>
                  <Col
                    span={4}
                    style={{
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilterSearch} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>
          {/* /Filter */}

          <CardWrapper>
            {customerPriceLists.length === 0 &&
            !this.props.customerPriceList.loadingGetCP ? (
              <Empty
                description={
                  <IntlMessages id="customerPriceLists.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loadingGetCP}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    customerPriceList: state.customerPriceList,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(CustomerPriceList);
